@use "../utilities/variables" as var;
@use "../utilities/mixins" as mix;
.vacancies {

	> .section__wrapper {
		gap: 32px;
		> .title {
			width: 731px;

			@include mix.maxw(var.$w-mob) {
				width: 100%;
			}

			> h1 {
				padding-top: 0;
			}
		}
	}

	&__button {
		z-index: 2;
	}

	&__accordion {

		> .accordion {
			margin-top: 64px;
			> h4 {
				@include mix.maxw(var.$w-mob) {
					font-size: 42px;
					line-height: 120%;
				}
			}
		}

	}

	@include mix.maxw(var.$w-mob) {
		.spacer--72,
		.spacer--120,
		.spacer--184 {
			height: 100px;
		}
	}

	.spacer--184 {
		height: 158px;
	}

	.cta {
		padding: 64px 20px 84px 20px;

		@include mix.maxw(var.$w-mob) {
			padding: 64px 20px 64px 20px;
		}
	}

}